@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
.homepage {
    background-image: url("https://i.pinimg.com/originals/75/5c/57/755c576d50d64770a04e703402eddfc3.jpg");
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    color: whitesmoke;
}

.landing {
    height: 100vh;
    background-image: url("/styley.png");
    background-repeat: no-repeat;
    background-size: cover;
    color: whitesmoke;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.text-focus-in {
    -webkit-animation: text-focus-in 2s cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
            animation: text-focus-in 2s cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
    font-size: 3.5em;
    justify-content: center;
    line-height: 95%;
    text-shadow: 1px 1px 2px black, 0 0 25px white;
    /* color: linear-gradient(to left, #FF0000 0%, #FFFF00 20%, #00FF00 40%, #00FFFF 60%, #0000FF 80%, #FF00FF 100%); */
}

.sub-title {
    margin-top: 5%;
    margin-bottom: 5%;
    font-size: 1.5em;
}

.landing-btn {
    border: 1px solid dodgerblue;
    border-radius: 15px;
    padding: 15px;
    background-color: whitesmoke;
    color: #6441a5;
    font-size: 1.3em;
    font-weight: bolder;
    box-shadow: 2px 5px;
}

.poti-card {
    background-color: #cdbce1;
    padding-top: 10vh;
    padding-bottom: 20vh;
    text-align: center;
}

.poti-card h2 {
    font-size: 4em;
    padding-bottom: 2%;
    text-shadow: 1px 1px 2px black, 0 0 25px #6441a5;
}

.poti-card span {
    font-size: 2em;
}

.toto-card {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    background-color: #cdbce1;
    padding-top: 20vh;
    padding-bottom: 15vh;
}

#showall>div {
    margin-top: 3%;
    margin-bottom: 2%;
    text-align: center;
}

@-webkit-keyframes text-focus-in {
    0% {
        -webkit-filter: blur(12px);
                filter: blur(12px);
        opacity: 0;
    }
    100% {
        -webkit-filter: blur(0px);
                filter: blur(0px);
        opacity: 1;
    }
}

@keyframes text-focus-in {
    0% {
        -webkit-filter: blur(12px);
                filter: blur(12px);
        opacity: 0;
    }
    100% {
        -webkit-filter: blur(0px);
                filter: blur(0px);
        opacity: 1;
    }
}
.streamer-list {
    display: grid;
    grid-gap: 15px;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    grid-template-rows: repeat(auto-fit, minmax(300px, 300px));
    justify-content: center;
    color: black
}

.streamer-list .ui.card {
    margin: 0;
}
.streamer-detail {
    text-align: center;
    padding-right: 60px;
    color: whitesmoke;
    margin-top: 1%;
    margin-bottom: 1%;
}

.streamer-detail h1 {
    text-shadow: 1px 1px 2px black, 0 0 25px white;
}

.streamer-detail p {
    font-size: 1.3em;
}
.edit-schedule-container {
   display: grid;
   grid-template-columns: repeat(1, 100%);
}

.display-schedule-container{
   background-color: #442d6b;
   margin-top: 5%;
}


.edit-schedule-image-span {
   margin-left: 1%;
   font-size: 1em;

}

.edit-schedule-btn-save {
   display: flex;
   justify-content: center;
}
ul li{
    float: left;
    width: 10%;
    background: #442d6b;
}

 ul{
    list-style-type: disc;
    background: #442d6b; 
}

.form-display-schedule-weekday {
    font-size: 2em;
   
    width: 130px;
    display: flex;
    justify-content: center;
}

.form-display-schedule-line-row {
    /* display: flex;
    flex-direction: row;
    margin-bottom: 5%; */
    margin: 2%;
    margin-bottom: 5%;
    display: grid;
    grid-gap: 30px;
    grid-template-columns: repeat(auto-fit, minmax(2000px, 7fr));
    
    width:  150px;
}

.form-display-schedule-card {
    width: 20%;
    height: 25%;
    display: flex;
    margin-left: 2%;
    flex-direction: column;
    border: 1px #442d6b solid;
    /* border-radius: 10px; */
}

.form-display-schedule-header {
    height: 30%;
    text-align: center;
    font-size: 12px;
    display: flex;
    flex-direction: row;
    /* border-radius: 10px; */
}

.form-display-schedule-container {
    padding: 2px 2px;
    display: flex;
    justify-content: center;
    height: 1px;
    margin: 0;
    align-items: center;
    
}

.form-display-schedule-content{
    padding: 10% 10%;
} 

.main-card {
    width: 170px;
    margin: 0px;
}

.container-card {
   color: "white";
   display: "flex";
   flex-direction: "column";
}

.fliptheCard {
    -webkit-animation: tilt-in-top-1 0.6s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
            animation: tilt-in-top-1 0.6s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
    width: 200px;
    padding: 5px;
    margin: 0;
}

.form-display-card {
    margin-left:5%;
    width: 90%;
}

@-webkit-keyframes tilt-in-top-1 {
    0% {
      transform: rotateY(30deg) translateY(-300px) skewY(-30deg);
      opacity: 0;
    }
    100% {
      transform: rotateY(0deg) translateY(0) skewY(0deg);
      opacity: 1;
    }
  }

@keyframes tilt-in-top-1 {
    0% {
      transform: rotateY(30deg) translateY(-300px) skewY(-30deg);
      opacity: 0;
    }
    100% {
      transform: rotateY(0deg) translateY(0) skewY(0deg);
      opacity: 1;
    }
  }
  
.one-schedule-global {
    height: 100vh;
    background-image: url("https://i.pinimg.com/originals/75/5c/57/755c576d50d64770a04e703402eddfc3.jpg");
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.one-schedule-h1 {
    text-align: center;
    margin: 50% 0;
}

.one-schedule-main-container {
    display: grid;
    grid-template-columns: repeat(2, 20%);
}

.one-schedule-user-card {
    margin-left: 10%;
    margin-top: 4%;
}

.one-schedule-wall-card {
    -webkit-animation: slit-in-vertical 1s ease-out both;
            animation: slit-in-vertical 1s ease-out both;
    width: 100vw;
    margin-top: 4%;
}

.one-schedule-img-container {
    width: 75%;
}

@-webkit-keyframes slit-in-vertical {
    0% {
        transform: translateZ(-800px) rotateX(90deg);
        opacity: 0;
    }
    54% {
        transform: translateZ(-160px) rotateX(87deg);
        opacity: 1;
    }
    100% {
        transform: translateZ(0) rotateX(0);
    }
}

@keyframes slit-in-vertical {
    0% {
        transform: translateZ(-800px) rotateX(90deg);
        opacity: 0;
    }
    54% {
        transform: translateZ(-160px) rotateX(87deg);
        opacity: 1;
    }
    100% {
        transform: translateZ(0) rotateX(0);
    }
}
*,*::before,*::after{box-sizing:border-box}ul[class],ol[class]{padding:0}body,h1,h2,h3,h4,p,ul[class],ol[class],figure,blockquote,dl,dd{margin:0}html{scroll-behavior:smooth}body{min-height:100vh;text-rendering:optimizeSpeed;line-height:1.5}ul[class],ol[class]{list-style:none}a:not([class]){-webkit-text-decoration-skip:ink;text-decoration-skip-ink:auto}img{max-width:100%;display:block}article>*+*{margin-top:1em}input,button,textarea,select{font:inherit}img:not([alt]){-webkit-filter:blur(10px);filter:blur(10px)}@media(prefers-reduced-motion:reduce){*{-webkit-animation-duration:.01ms !important;animation-duration:.01ms !important;-webkit-animation-iteration-count:1 !important;animation-iteration-count:1 !important;transition-duration:.01ms !important;scroll-behavior:auto !important}}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
}

html {
    font-family: "Manrope", sans-serif;
}

a {
    text-decoration: none;
    color: inherit;
}

ul {
    list-style-type: none;
}

.margin-left {
    margin-left: 2%;
}

.horizontal {
    display: flex;
}
